/**
 * @module 【ui】PageManager
 * @desc 交易页面管理器
 */
export default {
  pages: {},
  install(Vue, options) {
    console.log('PageManager: installing...');

    if (options && options.pages) {
      options.pages.forEach(page => {
        console.log('PageManager: registering %s', page.path);
        this.pages[page.path] = page.page;
      });
    }

    console.log('PageManager: installed');
  },
  /**
   * @func getPage
   * @desc 获取页面
   * @param {String} path - 页面路径
   * @returns {Object} 页面信息
   * @example import { PageManager } from 'awp-ui-page-manager'
   *
   * PageManager.getPage(path)
   */
  getPage(path) {
    console.log('Getting "%s"', path);
    let page = this.pages[path];
    return page;
  }
};
